import React from 'react'
import { Container, Box } from '@mui/material'

export function Hero() {
    return (
        <Box sx={{
            backgroundAttachment: 'fixed',
            textAlign: 'center',
            width: '100%',
            height: '90vh',
            background: 'top center no-repeat',
            backgroundImage: 'linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url("./assets/img/hero-background.jpg")',
            backgroundSize: 'cover',
        }}>
            <Container>
                <div id="hero-section">
                    <div id="hero-title-container" class='center'>
                        <h1>Cartera creativa Jacas Marketing</h1>
                        <p>En Jacas Marketing ofrecemos una amplia variedad de servicios para satisfacer las
                            necesidades de nuestros clientes. Nuestra cartera de servicios incluye:</p>
                    </div>
                </div>
            </Container>
        </Box>
    )
}
