import React from 'react'
import { Box, Container, useMediaQuery } from '@mui/material'
import { SimpleCard } from './Cards'


export function WebSection() {
    const query400px = useMediaQuery('(min-width:400px)');
    const query900px = useMediaQuery('(min-width:900px)');
    let cards_container_style = {
        display: 'flex',
        justifyContent: 'center',
    }
    if (!query400px) {
        cards_container_style['flexDirection'] = 'column'
    }
    else {
        cards_container_style['flexDirection'] = 'row'
    }

    if (!query900px) {
        cards_container_style['flexWrap'] = 'wrap'
    }
    else {
        cards_container_style['flexWrap'] = 'nowrap'
    }

  return (
    <Box sx={{
        padding: '5rem 0 5rem 0',
    }}>
        <Container>
            <div>
                <h1>Desarrollo Web</h1>
                <p>Creamos sitios web de alta calidad y completamente personalizados para satisfacer las necesidades de tu empresa. Ofrecemos una amplia variedad de servicios incluyendo diseo de sitios web, programacin, alojamiento y ms.</p>
            </div>
            <Box sx={cards_container_style}>
                <SimpleCard title = 'Sitios web personalizados' content = 'Creamos un sitio web para tu empresa que se adapte perfectamente a lo que necesitas.'/>
                <SimpleCard title = 'Seguridad del sitio web' content = 'Cuidamos la seguridad y la integridad de su sitio web para evitar posibles ataques.'/>
                <SimpleCard title = 'Diseo de Responsive Web' content = 'Realizamos sitios web adaptativos para mejorar la experiencia de los usuarios.'/>
            </Box>
        </Container>
    </Box>
  )
}
