import React from 'react'
import { Box, Container, useMediaQuery } from '@mui/material'
import { ImageCard } from './Cards'

export function DesignSection() {
    const query400px = useMediaQuery('(min-width:400px)');
    const query900px = useMediaQuery('(min-width:900px)');
    let cards_container_style = {
        display: 'flex',
        justifyContent: 'center',
    }
    if (!query400px) {
        cards_container_style['flexDirection'] = 'column'
    }
    else {
        cards_container_style['flexDirection'] = 'row'
    }

    if (!query900px) {
        cards_container_style['flexWrap'] = 'wrap'
    }
    else {
        cards_container_style['flexWrap'] = 'nowrap'
    }

    return (
        <Box sx={{
            padding: '5rem 0 5rem 0',
        }}>
            <Container>
                <h1>Diseo Grfico</h1>
                <p>Nuestro equipo de diseadores grficos expertos te brinda soluciones imaginativas, frescas y
                    atractivas para representar tu marca de la mejor manera posible. Ofrecemos servicios
                    personalizados de diseo para diversos medios.</p>
            </Container>
            <Box sx={cards_container_style}>
                <ImageCard title='Diseo de Logotipos' content='Logo diseado a medida para tu empresa, creativo y nico.' image='./assets/img/logo-design.jpg' />
                <ImageCard title='Diseo Publicitario' content='Publicidad impresa y digital en tu empresa con un diseo llamativo.' image='./assets/img/times-square-publicidad.jpg' />
            </Box>
        </Box>
    )
}
